<template>
  <v-container>
    <v-row> <v-col class="text-h5 font-weight-bold mt-3">회원정보 수정</v-col> </v-row>
    <div class="pa-2 mt-5">
      <v-row> <v-col class="text-h5 font-weight-regular">회원정보</v-col> </v-row>
      <v-row class="row-underline mt-5" no-gutters justify="center" align="center">
        <v-col>{{ member.id }} </v-col>
      </v-row>
      <v-row class="row-underline mt-5" no-gutters justify="center" align="center">
        <v-col>{{ member.name }} </v-col>
      </v-row>
      <v-form ref="form" lazy-validation>
        <v-row class="mt-5" no-gutters justify="center" align="center">
          <v-text-field v-model="member.phone" class="pa-0" :rules="phoneRule" label="phone"></v-text-field>
        </v-row>
        <v-row no-gutters justify="center" align="center">
          <v-text-field v-model="member.email" class="pa-0" :rules="emailRules" label="email"></v-text-field>
        </v-row>
      </v-form>
      <v-row align="center" justify="center" c>
        <yes-no-dialog :title="'변경'" :content="'정말로 변경하시겠습니까?'" @choose="moidfyMember" />
      </v-row>
    </div>
    <div class="text-center ma-2">
      <v-snackbar v-model="snackbar">
        {{ resultInfo }}
        <template v-slot:action="{ attrs }">
          <v-btn color="pink" text v-bind="attrs" @click="snackbar = false"> Close </v-btn>
        </template>
      </v-snackbar>
    </div>
  </v-container>
</template>

<script>
import MemberApi from "@/apis/member";
import YesNoDialog from "@/components/dialog/YesNoDialog.vue";
export default {
  components: { YesNoDialog },
  name: "Modify",
  data: () => ({
    snackbar: false,
    resultInfo: "",
    member: {},
    phoneRule: [
      (value) => {
        const pattern = /^(01)[0-9]{1}-[0-9]{3,4}-[0-9]{4}$/;
        return pattern.test(value) || "핸드폰 번호 형식이 아닙니다.";
      },
    ],
    emailRules: [
      (value) => !!value || "아이디를 입력해 주세요.",
      (value) => (value || "").length <= 20 || "20자를 넘을 수 없습니다.",
      (value) => {
        const pattern =
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(value) || "이메일 형식이 아닙니다.";
      },
    ],
  }),
  methods: {
    async moidfyMember() {
      if (this.$refs.form.validate()) {
        try {
          let promise = await MemberApi.modifyMember(this.member);
          if (promise.data == "success") {
            this.resultInfo = "변경되었습니다.";
          } else {
            this.resultInfo = "실패하였습니다.";
          }
          this.snackbar = true;
        } catch (error) {
          console.log(error);
        }
      }
    },
    async getMemberInfo() {
      try {
        let promise = await MemberApi.getMember(this.$store.getters.getMno);
        this.member = promise.data;
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.getMemberInfo();
  },
};
</script>

<style scpoed>
.row-underline {
  border-bottom: #ddd solid 2px;
  color: #666;
}
</style>
